<template>
	<view>
		<button @tap="chooseImg">xuanze</button>
		<image :src="'data:image/png;base64,' + img64"></image>
	</view>
</template>

<script>
	import { pathToBase64 } from '../../components/image-tools'
	export default {
		data() {
			return {
				sendimg:'',
				img64:'',
				img:false,
			}
		},
		methods: {
			
			async submit(){
				//let img64=''//await 
				await pathToBase64(this.sendimg).then(base64=>{
					
					base64 = base64.replace("data:image/png;base64,","");
					base64 = base64.replace("/[\r\n]/g","");
					this.img64=base64
				})
				//console.log("/data:image//png;base64,/")
				//console.log(this.img64)
				this.img=true
				//console.log(this.sendimg)
				//console.log(JSON.stringify(img64))
				//this.sendimg64=img64;
				//console.log(this.sendimg64)
				
			},
			chooseImg(){
				uni.chooseImage({
					count:1,
					success: (res) => {
						//console.log(res.tempFilePaths[0])
						this.sendimg=res.tempFilePaths[0]
						//console.log(this.sendimg)
						this.submit()
					}
				})
			},
		}
	}
</script>

